  $.fn.sliderFunc=function() {
    var _super=this;
	var animated = false;
    var _const=_super.children().length;
	var timer;
    var _point='point';
	var index = 0; //记录点击
	var sliderWidth=Math.round(parseInt($(_super).width(),10))/3;
    var _stat=false ;
    //将轮播图最后一个移动到第一个

	var Children = $(_super).children();
    var p=1;//错位个数
	var start={},
		current={};
    //动态设置滚动元素的宽度，left和index编号
    $.each(Children,function(i){
         $(this).attr({'index':(i)});
         $(this).css({'width':(sliderWidth + 'px'),'left':($(this).attr('index')*sliderWidth + 'px')});
         $(this).attr({'index':i});
        // $(this).html($(this).attr('index'));//输出当前页号
    })
    if (_const>3) {
        autoPlay(sliderWidth);
       };

    //向左滚动一页
    function Arrow_r(sw) {
    	console.log('Arrow_r'+index);
        if (!animated) {
            index++;
           if(index==_const){
                index=0;
            }
            console.log("当前页面"+index);
            animate(-sw);
        }
    }
    //向右滚动一页
    function Arrow_l(sw) {
    	console.log('Arrow_l'+index);
        if (!animated) {
            index--;
            if (index==-1) {
                index=_const-1;
            }
            
            animate(sw);
        }
    }
    //自动向左滚动
    function autoPlay(sw) {
    	console.log('autoPlay'+index);
        timer = setInterval(function() {
            Arrow_r(sw);
        }, 5000)
    }
    //停止自动滚动
    function stopAuto() {
    	console.log('stopAuto');
        clearInterval(timer)
    }
    //滚动函数
    function animate(wid) {//wid是绝对偏移量，带符号
        animated = true;
        stopAuto();
        if (wid == 0) {
            return
        };
        var time =sliderWidth/1000;//滚动时间
        var left; //目的偏移量
        var leftCorrt;//当前偏移量
        var stat=0;//记录动画次数

         
        $.each(Children,function () {        
            leftCorrt=parseInt($(this).css('left'),10);
            left = leftCorrt + wid;
            //移动
            $(this).animate({left:left},{quequ:false,complete:function(){
                stat++;
                if (stat==_const) {//监听移动次数以便允许用户其他操作
                    animated=false;
                    autoPlay(sliderWidth);
                    Children.filter(function(){
                       if(parseInt($(this).css('left'),10)==-sliderWidth){
                                return $(this);
                            }
                    }).css('left',(_const-1)*sliderWidth)

                    };
                }}
            );
         
        });
    }
}


