  $.fn.inforSlider=function(_stat) {
    var _super=this;
    var animated = false;
    var _const=_super.children().length;
    var timer;
    var _point='point';
    var index = 0; //记录点击
    var sliderWidth=Math.round(parseInt($('.container').width()/_stat,10));

    var Children = $(_super).children();
    var p=1;//错位个数
    var start={},
        current={};
    //动态设置滚动元素的宽度，left和index编号
    $.each(Children,function(i){
         $(this).attr({'index':(i)});
         $(this).css({'width':(sliderWidth + 'px'),'left':(($(this).attr('index')-1)*sliderWidth + 'px')});
    })
    $(_super).parent().children().filter('.arr-l').click(function(){
        Arrow_l(sliderWidth);
    })
    $(_super).parent().children().filter('.arr-r').click(function(){
        Arrow_r(sliderWidth);
    })
  
    //向左滚动一页
    function Arrow_r(sw) {
        if (!animated) {
            if (parseInt(Children.eq(_const-1).css('left'),10)==(_stat-1)*sliderWidth) {return};
            index++;
           if(index==_const){
                index=0;
            }
            console.log("当前页面"+index);
            animate(-sw);
        }
    }
    //向右滚动一页
    function Arrow_l(sw) {
        if (!animated) {
            if (parseInt(Children.eq(0).css('left'),10)==0) {return};
            index--;
            if (index==-1) {
                index=_const-1;
            }
            
            animate(sw);
        }
    }
    //滚动函数
    function animate(wid) {//wid是绝对偏移量，带符号
        animated = true;
        if (wid == 0) {
            return
        };
        var time =sliderWidth/1000;//滚动时间
        var left; //目的偏移量
        var leftCorrt;//当前偏移量
        var stat=0;//记录动画次数

         
        $.each(Children,function () {        
            leftCorrt=parseInt(this.style.left);
            left = leftCorrt + wid;
            //移动
            $(this).animate({left:left},{quequ:false,complete:function(){
                stat++;
                if (stat==_const) {//监听移动次数以便允许用户其他操作
                    animated=false;
                    };
                }}
            );
            
        });
    }
}


